/* eslint-disable */

import React, { useEffect, useState }  from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
    padding: 20px;
`;
let 제목 = styled.h4`
    font-size: 25px;
    color: ${ props => props.색상 };
`;

function Detail(props){

    useEffect(()=>{
        console.log(11111);
        let 타이머 = setTimeout(()=>{ alert변경(false)},2000);
        return ()=>{
            clearTimeout(타이머)
        }
    },[]);

    let [alert,alert변경] = useState(true);
    let history = useHistory();
    let { id } = useParams();

    let thisItem = props.shoes.find( function(item){
        return item.id == id;
    })

    let [cinput,cinput변경] = useState('');    
    return(
        <div className="container">
        <박스>
            <제목 색상={'red'}>ssss</제목>
            <제목 색상='blue'>ssss</제목>
            <제목 className='red'>ssss</제목>
        </박스>
        <input onChange={(e)=>{cinput변경(e.target.value)}}/>

        {
            alert === true ? <div className='my-alert2'>재고가 얼마 남지 않았습니다.</div> : null
        }
        
        <div className="row">
          <div className="col-md-6">
            <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{thisItem.title}</h4>
            <p>{thisItem.content} </p>
            <p>{thisItem.price}원</p>
            <Info 재고={props.재고}></Info>
            <button className="btn btn-danger">주문하기</button> 
            <button className="btn btn-danger" onClick={ ()=>{
                history.push('/');
            }}>뒤로가기</button> 
          </div>
        </div>
       </div> 
    )
}

function Info(props){
    return (
        <p>재고: {props.재고[0]}</p>
    )
}

export default Detail;